@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-image:url(/aprozar-la-garaj/static/media/home.657bfcef.jpeg);background-position:center;font-family:'Comfortaa', cursive;font-size:16px;color:white}.App{height:100vh;overflow-y:scroll}.main{background-image:linear-gradient(to right, #2e2d2d, #35352d);min-height:100vh;padding:6vh 2em 0vh 2em;color:white}*{box-sizing:border-box}.center{text-align:center}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{color:white;width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:300px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:51px;right:10px;color:black}.cart-dropdown .empty-cart-message{font-size:18px;margin:auto}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow-y:scroll}.cart-dropdown button{margin-top:auto}

.header{position:fixed;z-index:1;height:50px;width:100%;display:flex;background-image:linear-gradient(to right, #2e2d2d, #35352d)}.header .navigation{width:100%;height:100%;display:flex;align-items:center;justify-content:flex-end;float:right}.header .navigation .navigation-link{padding:10px 15px;color:white;text-decoration:none;cursor:pointer}

.particles{position:fixed;top:0;bottom:0;right:0;left:0;z-index:-1}header{height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;line-height:7vh}header h1{font-size:10vw;font-family:'Pacifico', cursive;color:white;text-shadow:1px 1px 1px rgba(0,0,0,0.4)}header h2{font-family:'Comfortaa', cursive;font-feature-settings:normal;font-variant-caps:normal;font-size:3vw;padding:1em;background:linear-gradient(to right, #2e2d2d, #35352d);color:yellow;z-index:-1}

.card{width:200px;display:flex;flex-direction:column;height:200px;align-items:center;margin:5px 5px 0px 0px;position:relative}.card .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.card .custom-button{width:80%;opacity:.7;position:absolute;top:50px;display:none}.card:hover .image{opacity:.6}.card:hover .custom-button{opacity:.85;display:flex}.card .collection-footer{width:100%;height:25%;display:flex;justify-content:space-between;font-size:18px}.card .collection-footer .name{width:80%;margin-bottom:15px}.card .collection-footer .price{width:20%}

.card-list{display:flex;flex-wrap:wrap}

.search-box{width:100%;text-align:center}.search-box .search-field{width:75%;border-radius:4px;padding:5px}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:10%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.checkout-page{width:100%;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}

.product-page{width:100%;display:flex;flex-wrap:wrap}.product-page .shop-list{float:left;order:1;min-width:400px;width:50%}.product-page .shop-list .checkout-list{width:90%;display:flex;flex-direction:column;align-items:center;margin:0 auto 0}.product-page .product-list{float:left;order:2;min-width:500px;width:50%}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:#353535;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid #353535;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-18px;font-size:14px;color:#fff}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:#353535;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-18px;font-size:14px;color:#fff}

.sign-in{display:flex;flex-direction:column;padding:0 1em;float:left;width:450px;min-width:300px}.buttons{display:flex;justify-content:space-between}

.sign-up{display:flex;flex-direction:column;padding:0 1em;float:left;width:450px;min-width:300px}.sign-up .title{margin:10px 0}

.login-register{display:flex;justify-content:space-around;flex-wrap:wrap}

.main .checkout-list{width:55%;display:flex;flex-direction:column;align-items:center;margin:0 auto 0}

